    "code": "import React from 'react';\nimport PropTypes from 'prop-types';\nimport { SearchInput, SearchWrapper } from './style';\n\n/** Search input that filters the Select items and adds keyboard navigation */\nexport default class Search extends React.Component {\n  state = {\n    search: '',\n  };\n\n\n  componentDidUpdate(prevProps) {\n    if(prevProps.isOpen !== this.props.isOpen){\n      setTimeout(()=> this.inputRef.focus(), 50)\n    }\n  }\n\n\n  onChange = (event) => {\n    const { onChange } = this.props;\n    const search = event.target.value;\n\n    onChange(search);\n    this.setState({\n      search,\n    });\n  }\n\n  render() {\n    const {\n      placeholder,\n    } = this.props;\n\n    const { search } = this.state;\n\n    return (\n      <SearchWrapper>\n        <SearchInput\n          placeholder={placeholder}\n          type=\"text\"\n          value={search}\n          ref={inputRef => this.inputRef = inputRef}\n          onChange={event => this.onChange(event)}\n        />\n      </SearchWrapper>\n    );\n  }\n}\n\nSearch.propTypes = {\n  /** Search placeholder */\n  placeholder: PropTypes.string,\n\n  /** Function to call on search input change */\n  onChange: PropTypes.func.isRequired,\n\n  /** Is the select menu open */\n  isOpen: PropTypes.bool\n};\n\nSearch.defaultProps = {\n  placeholder: '',\n  isOpen: false\n}\n",
